import React, { useState } from 'react';
import { Row, Col, Card, Form, Button, Alert } from 'react-bootstrap';
import { lifeTimelineEvents, introGuide } from '../../data/SelfExplorationData';

const ReflectionJournal: React.FC = () => {
  const [journalEntries, setJournalEntries] = useState([
    {
      id: 1,
      date: '2025-05-25',
      title: '',
      content: '"" ' ,
      tags: ['', '', '']
    },
    {
      id: 2,
      date: '2025-05-22',
      title: '',
      content: '',
      tags: ['', '', '']
    }
  ]);

  const [newEntry, setNewEntry] = useState({
    title: '',
    content: '',
    tags: ''
  });

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setNewEntry({
      ...newEntry,
      [name]: value
    });
  };

  const handleAddEntry = (e: React.FormEvent) => {
    e.preventDefault();
    // 
    const newEntryWithId = {
      ...newEntry,
      id: journalEntries.length + 1,
      date: new Date().toISOString().split('T')[0],
      tags: newEntry.tags.split(',').map(tag => tag.trim())
    };

    setJournalEntries([newEntryWithId, ...journalEntries]);
    setNewEntry({
      title: '',
      content: '',
      tags: ''
    });
  };

  return (
    <div className="reflection-journal-section">
      <h2></h2>
      <p className="lead"></p>

      <Card className="mb-4">
        <Card.Body>
          <h4>{introGuide.title}</h4>
          {introGuide.paragraphs.map((paragraph, index) => (
            <p key={index}>{paragraph}</p>
          ))}
          <ul>
            {introGuide.listItems.map((item, index) => (
              <li key={index}>{item}</li>
            ))}
          </ul>
          <Alert variant="info">
            <strong></strong> {introGuide.tip}
          </Alert>
        </Card.Body>
      </Card>

      <Card className="mb-4">
        <Card.Body>
          <h4></h4>
          {lifeTimelineEvents.map((event) => (
            <div key={event.id} className="mb-3 p-3 border rounded bg-light">
              <h5>{event.age}: {event.title}</h5>
              <p><strong>:</strong> {event.description}</p>
              <p><strong>:</strong> {event.impact}</p>
              <p><strong>:</strong> {event.emotions.join(', ')}</p>
              <div className="d-flex justify-content-end">
                <Button variant="link" className="p-0 me-2"></Button>
                <Button variant="link" className="p-0 text-danger"></Button>
              </div>
            </div>
          ))}
        </Card.Body>
      </Card>

      <h2></h2>
      <p className="lead"></p>
      
      <Row>
        <Col md={8}>
          <Card className="mb-4">
            <Card.Body>
              <h4></h4>
              <Form onSubmit={handleAddEntry}>
                <Form.Group className="mb-3">
                  <Form.Label></Form.Label>
                  <Form.Control 
                    type="text" 
                    name="title" 
                    value={newEntry.title}
                    onChange={handleInputChange}
                    placeholder="" 
                  />
                </Form.Group>
                
                <Form.Group className="mb-3">
                  <Form.Label></Form.Label>
                  <Form.Control 
                    as="textarea" 
                    rows={6} 
                    name="content" 
                    value={newEntry.content}
                    onChange={handleInputChange}
                    placeholder="..." 
                  />
                </Form.Group>
                
                <Form.Group className="mb-3">
                  <Form.Label></Form.Label>
                  <Form.Control 
                    type="text" 
                    name="tags" 
                    value={newEntry.tags}
                    onChange={handleInputChange}
                    placeholder=", : , , " 
                  />
                </Form.Group>
                
                <Button variant="primary" type="submit">
                  
                </Button>
              </Form>
            </Card.Body>
          </Card>
          
          <div className="journal-entries">
            <h4></h4>
            
            {journalEntries.map(entry => (
              <Card key={entry.id} className="mb-3">
                <Card.Body>
                  <div className="d-flex justify-content-between align-items-start">
                    <div>
                      <h5>{entry.title}</h5>
                      <p className="text-muted mb-2">{entry.date}</p>
                      <p>{entry.content}</p>
                      <div>
                        {entry.tags.map((tag, i) => (
                          <span key={i} className="badge bg-light text-dark me-2 mb-2">{tag}</span>
                        ))}
                      </div>
                    </div>
                    <div className="journal-entry-actions">
                      <Button variant="link" className="p-0 me-2"></Button>
                      <Button variant="link" className="p-0 text-danger"></Button>
                    </div>
                  </div>
                </Card.Body>
              </Card>
            ))}
          </div>
        </Col>
        
        <Col md={4}>
          <Card className="mb-4">
            <Card.Body>
              <h4></h4>
              <p></p>
              
              <div className="reflection-prompts">
                <div className="prompt-item p-3 bg-light rounded mb-2">
                  <p className="mb-0"></p>
                </div>
                
                <div className="prompt-item p-3 bg-light rounded mb-2">
                  <p className="mb-0"></p>
                </div>
                
                <div className="prompt-item p-3 bg-light rounded mb-2">
                  <p className="mb-0"></p>
                </div>
                
                <div className="prompt-item p-3 bg-light rounded mb-2">
                  <p className="mb-0"></p>
                </div>
              </div>
            </Card.Body>
          </Card>
          
          <Card>
            <Card.Body>
              <h4></h4>
              <div className="tag-cloud p-3">
                <span className="badge bg-primary me-2 mb-2 p-2"></span>
                <span className="badge bg-secondary me-2 mb-2 p-2"></span>
                <span className="badge bg-success me-2 mb-2 p-2"></span>
                <span className="badge bg-danger me-2 mb-2 p-2"></span>
                <span className="badge bg-warning text-dark me-2 mb-2 p-2"></span>
                <span className="badge bg-info text-dark me-2 mb-2 p-2"></span>
                <span className="badge bg-light text-dark me-2 mb-2 p-2"></span>
                <span className="badge bg-dark me-2 mb-2 p-2"></span>
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default ReflectionJournal;