import React from 'react';
import { Container, Row, Col, Card, Button, Image, Tab, Tabs } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import styles from './Home.module.css';

const Home: React.FC = () => {
  return (
    <Container fluid className="py-5 px-4" style={{ backgroundColor: '#fffdf9' }}>

      {/*  */}
      <Row className="mb-5 align-items-center">
        <Col md={6}>
          <h1 className="display-4 mb-3" style={{ color: '#FF7F50' }}></h1>
          <p className="lead">
            
            
          </p>
          <div className="d-flex gap-3 mt-4">
            <Button as={Link} to="/knowledge" size="lg" style={{ backgroundColor: '#FFB347', border: 'none' }}></Button>
            <Button as={Link} to="/resources" size="lg" variant="outline-primary"></Button>
          </div>
        </Col>
        <Col md={6} className="text-center">
          <Image src="/images/yezi.jpg" roundedCircle fluid style={{ maxWidth: '70%' }} />
        </Col>
      </Row>

      {/*  */}
      <Row className="my-5 py-5" style={{ backgroundColor: '#FFF4E0' }}>
        <Col md={12} className="text-center">
          <h2 className="fw-bold mb-4" style={{ color: '#444' }}>
            
          </h2>
          <p className="lead text-muted" style={{ maxWidth: '720px', margin: '0 auto' }}>
            
          </p>
        </Col>
      </Row>

      {/*  */}
      <Row className="mb-5">
        <Col md={12} className="text-center mb-4">
          <h2 className="fw-bold"></h2>
          <p className="text-muted"></p>
        </Col>
        <Col md={12}>
          <Row className="g-4">
            {[
              ['', ''],
              ['', ''],
              ['', ''],
              ['', ''],
              ['', '']
            ].map(([title, desc], idx) => (
              <Col md={4} key={idx}>
                <Card className="h-100 shadow-sm border-0">
                  <Card.Body>
                    <div className="mb-3 d-flex align-items-center">
                      <div className="me-3 rounded-circle d-flex align-items-center justify-content-center" style={{ width: '40px', height: '40px', backgroundColor: '#FFA07A', color: 'white' }}>{idx + 1}</div>
                      <Card.Title className="mb-0 fw-bold">{title}</Card.Title>
                    </div>
                    <Card.Text className="text-muted">{desc}</Card.Text>
                  </Card.Body>
                </Card>
              </Col>
            ))}
          </Row>
        </Col>
      </Row>

      {/*  */}
      <Row className="mb-5">
        <Col md={12} className="text-center mb-4">
          <h2 className="fw-bold"></h2>
          <p className="text-muted"></p>
        </Col>
        <Tabs defaultActiveKey="features" className="mb-4 justify-content-center">
          <Tab eventKey="features" title="">
            <Row className="g-4">
              {[
                ['', '', '/exploration'],
                ['', '', '/tracking'],
                ['', '', '/']
              ].map(([title, desc, link], idx) => (
                <Col md={4} key={idx}>
                  <Card className="h-100 border-0 shadow-sm">
                    <Card.Body>
                      <Card.Title className="fw-bold" style={{ color: '#333' }}>{title}</Card.Title>
                      <Card.Text className="text-muted">{desc}</Card.Text>
                      <Button as={Link} to={link} variant="link"> </Button>
                    </Card.Body>
                  </Card>
                </Col>
              ))}
            </Row>
          </Tab>
          <Tab eventKey="resources" title="">
            <Row className="g-4 mt-3">
              {[
                ['', '', '/knowledge'],
                ['', '', '/resources'],
                ['', '', '/practice-guides']
              ].map(([title, desc, link], idx) => (
                <Col md={4} key={idx}>
                  <Card className="h-100 border-0 shadow-sm">
                    <Card.Body>
                      <Card.Title className="fw-bold" style={{ color: '#333' }}>{title}</Card.Title>
                      <Card.Text className="text-muted">{desc}</Card.Text>
                      <Button as={Link} to={link} variant="link"> </Button>
                    </Card.Body>
                  </Card>
                </Col>
              ))}
            </Row>
          </Tab>
        </Tabs>
      </Row>

    </Container>
  );
};

export default Home;
