import React, { useEffect } from 'react';
// d3
import * as d3 from 'd3';
// : npm install --save-dev @types/d3
import './ConceptGraph.css';

interface ConceptGraphProps {
  width?: number;
  height?: number;
}

const ConceptGraph: React.FC<ConceptGraphProps> = ({ width = 800, height = 500 }) => {
  useEffect(() => {
    // 
    const graphData = {
      "nodes": [
        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},

        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},

        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},

        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},
        {"id": "PTSD", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},

        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""},

        {"id": "", "group": "", "description": ""},
        {"id": "", "group": "", "description": ""}
      ],
      "links": [
        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "", "type": ""},

        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "", "type": ""},

        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "", "type": ""},

        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "PTSD", "type": ""},
        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "", "type": ""},

        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "", "type": ""},

        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "", "type": ""},
        {"source": "", "target": "", "type": ""}
      ]
    };

    // SVG
    const svg = d3.select('#concept-graph')
      .attr('width', width)
      .attr('height', height);

    // 
    svg.selectAll('*').remove();

    // 
    const g = svg.append('g');

    // 
    const color = d3.scaleOrdinal(d3.schemeCategory10);

    // 
    const simulation = d3.forceSimulation(graphData.nodes as d3.SimulationNodeDatum[])
      .force('link', d3.forceLink(graphData.links).id((d: any) => d.id).distance(100))
      .force('charge', d3.forceManyBody().strength(-300))
      .force('center', d3.forceCenter(width / 2, height / 2));

    // 
    const link = g.append('g')
      .attr('class', 'links')
      .selectAll('line')
      .data(graphData.links)
      .join('line')
      .attr('class', 'link')
      .attr('stroke-width', 1.5);

    // 
    const node = g.append('g')
      .attr('class', 'nodes')
      .selectAll('circle')
      .data(graphData.nodes)
      .join('circle')
      .attr('r', 10)
      .attr('fill', (d: any) => color(d.group))
      .attr('class', 'node-circle')
      .call(drag(simulation) as any);

    // 
    const labels = g.append('g')
      .attr('class', 'labels')
      .selectAll('text')
      .data(graphData.nodes)
      .join('text')
      .text((d: any) => d.id)
      .attr('class', 'node-label')
      .attr('dx', 12)
      .attr('dy', 4);

    // 
    const tooltip = d3.select('#concept-tooltip');

    node.on('mouseover', function(event: any, d: any) {
      tooltip.transition()
        .duration(200)
        .style('opacity', 0.9);
      tooltip.html(`<strong>${d.id}</strong><br/>${d.description || ''}`)
        .style('left', (event.pageX + 10) + 'px')
        .style('top', (event.pageY - 28) + 'px');
    })
    .on('mouseout', function() {
      tooltip.transition()
        .duration(500)
        .style('opacity', 0);
    });

    // 
    simulation.on('tick', () => {
      link
        .attr('x1', (d: any) => d.source.x)
        .attr('y1', (d: any) => d.source.y)
        .attr('x2', (d: any) => d.target.x)
        .attr('y2', (d: any) => d.target.y);

      node
        .attr('cx', (d: any) => d.x)
        .attr('cy', (d: any) => d.y);

      labels
        .attr('x', (d: any) => d.x)
        .attr('y', (d: any) => d.y);
    });

    // 
    function drag(simulation: any) {
      function dragstarted(event: any, d: any) {
        if (!event.active) simulation.alphaTarget(0.3).restart();
        d.fx = d.x;
        d.fy = d.y;
      }

      function dragged(event: any, d: any) {
        d.fx = event.x;
        d.fy = event.y;
      }

      function dragended(event: any, d: any) {
        if (!event.active) simulation.alphaTarget(0);
        d.fx = null;
        d.fy = null;
      }

      return d3.drag()
        .on('start', dragstarted)
        .on('drag', dragged)
        .on('end', dragended);
    }

    // 
    const zoom = d3.zoom()
      .scaleExtent([0.5, 8])
      .on('zoom', (event: any) => {
        g.attr('transform', event.transform);
      });

    svg.call(zoom as any);

    return () => {
      simulation.stop();
    };
  }, [width, height]);

  return (
    <div className="concept-map-section">
      <h2></h2>
      <p></p>
      
      <div className="controls mb-3">
        <label htmlFor="theory-filter">:</label>
        <select id="theory-filter" className="form-select">
          <option value="all"></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
      </div>
      
      <div id="concept-graph-container">
        <svg id="concept-graph" width={width} height={height}></svg>
        <div id="concept-tooltip" className="tooltip" style={{ opacity: 0 }}></div>
      </div>
      
      <div className="ai-disclaimer mt-3">
        <p><strong></strong>AI</p>
      </div>
    </div>
  );
};

export default ConceptGraph;